/*Top Section Design: Carousel*/
#slider{
  z-index:-5;
}
.owl-item {
  -webkit-backface-visibility: hidden;
  -webkit-transform: translateZ(0) scale(1.0, 1.0);
}
#slider img{
  display: block;
  width: 100%;
  height: auto;
}
#slider .owl-item:before{
  content:"";
  position:absolute;
  top:0px;
  left:0px;
  width:100%;
  height:100%;
  background-color:rgba(0,0,0,0.35);
}
.owl-dots{
  width: 100%;
  position: relative;
  top: 50px;
  height: auto;
  display: inline-block;
  float:left;
  text-align: center;
  margin: 0 auto;
}
.owl-dots .owl-dot.active{
  color: white;
  text-shadow: -1px 0 black, 0 1px black, 1px 0 black, 0 -1px black;
}
.top-section{
  max-height: 100vh;
  z-index: -100;
}
.slide-caption {
  position: absolute;
  top: 50%;
  left: 0;
  text-align: center;
  width: 100%;
  height: 100%;
