/*Slanted containers*/
.angular {
  position: relative;
  z-index: 0;
}
.white-bg.angular.last {
  margin-bottom: 0;
}
.white-bg.angular {
  margin-bottom: 170px;
}
.angular .top-angle,
.angular .bottom-angle {
  position: absolute;
  width: 100%;
  left: 0;
  height: 151px;
  overflow: hidden;
  z-index: 2;
}
.angular .top-angle {
  top: -150px;
}
.angular .bottom-angle {
  bottom: -150px;
}
.angular .top-angle:before,
.angular .bottom-angle:before {
  content: "";
  width: 100%;
  height: 150px;
  background: #ffffff;
  position: absolute;
  left: 0;
  -webkit-transform: rotate(-3deg) skew(-3deg) scale(1.1, 1);
  -mox-transform: rotate(-3deg) skew(-3deg) scale(1.1, 1);
  transform: rotate(-3deg) skew(-3deg) scale(1.1, 1);
}
.angular .top-angle:before {
    top: 75px;
}
.gray-bg.angular .top-angle:before {
    background: #ecf0f1;
}
.blue-bg.angular .top-angle:before {
    background: #0F6FA9;
}
.angular .bottom-angle:before {
    bottom: 75px;
}
.gray-bg.angular .bottom-angle:before {
    background: #ecf0f1;
}
.blue-bg.angular .bottom-angle:before {
    background: #0F6FA9;
}

