.post-tabs .tp-thumb {
    opacity: 1
}

.post-tabs .tp-thumb-over {
    background: #252525;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0px;
    left: 0px;
    z-index: 1;
    -webkit-transition: all 0.3s;
    transition: all 0.3s;
}

.post-tabs .tp-thumb-more:before {
    font-family: "revicons";
    font-size: 12px;
    color: #aaa;
    color: rgba(255, 255, 255, 0.75);
    display: block;
    line-height: 12px;
    text-align: left;
    z-index: 2;
    position: absolute;
    top: 15px;
    right: 15px;
    z-index: 2;
}

.post-tabs .tp-thumb-more:before {
    content: "\e825";
}

.post-tabs .tp-thumb-title {
    font-family: "raleway";
    letter-spacing: 1px;
    font-size: 12px;
    color: #fff;
    display: block;
    line-height: 15px;
    text-align: left;
    z-index: 2;
    position: absolute;
    top: 0px;
    left: 0px;
    z-index: 2;
    padding: 15px 30px 15px 15px;
    width: 100%;
    height: 100%;
    box-sizing: border-box;
    transition: all 0.3s;
    -webkit-transition: all 0.3s;
    font-weight: 500;
}

.post-tabs .tp-thumb.selected .tp-thumb-more:before,
.post-tabs .tp-thumb:hover .tp-thumb-more:before {
    color: #aaa;
}

.post-tabs .tp-thumb.selected .tp-thumb-over,
.post-tabs .tp-thumb:hover .tp-thumb-over {
    background: #fff;
}

.post-tabs .tp-thumb.selected .tp-thumb-title,
.post-tabs .tp-thumb:hover .tp-thumb-title {
    color: #000;
}